<template>
  <div class="square">
    <!-- <van-nav-bar            
        left-arrow
        title="E区专题"
        @click-left="onClickLeft"
    >    
    </van-nav-bar> -->
    <div class="bg">      
      <img class="bg-image" :src="pic" >
      <div class="other">
        <div class="views">{{views}}</div>
        <div class="replies">{{replies}}</div>
      </div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="list"  v-for="(item,key) in datalist " :key="key" >
        <div v-if="item.ad == 1" class="ad-box">
          <img :src="item.data" >
          <span class="">广告</span>
        </div>
        <div v-if="item.ad != 1" >
          <div class="header" > 
            <div class="avatar-box" @click="handleToMyPage(items)"> 
              <img :src="item.avatar" class="portrait_img" alt />
              <img class="isvip" src="../../assets/images/icon_isvip.png"  v-if="item.is_ident_icon == 1" >
            </div>
            <div class="header_center" @click="handleToDetails(item.tid)" >
              <span class="header_center_name">{{item.author}}</span>
              <div class="header_center_label">
                <div class="label_grade">
                  <img class="label_grade_img" :class="{'opacity_0':item.is_carvip != 1 }" v-if='item.is_carvip == 1' :src="item.is_carvip == 1 ? icon_iscarvip : ''" alt  />
                  <span class="label_grade_name" >{{item.level}}</span>
                </div>
                <div class="label_heat">
                  <img class="label_heat_img" :src='icon_label_d' alt />
                  <span class="label_grade_name">{{item.extcredits2}}°</span>
                </div>
              </div>
            </div>
            <van-icon class="more" size="30px" name="ellipsis"  @click="handleMore(item)" />
          </div>
          <div class="content" @click="handleToDetails(item.tid)" >
            <div :class="showTotal ? 'total-introduce' : 'detailed-introduce'">
            
              <div class="intro-content" :title="item.message" ref="desc">
                <span class="merchant-desc" v-if="item.message"><span class="name">{{item.class_name}}</span>{{item.message}}</span>
                <div class="unfold" @click="showTotalIntro" v-if="showExchangeButton">
                  <p>{{exchangeButton ? '展开' : '收起'}}</p>
                  <img :src="exchangeButton?down:up" alt="">
                </div>
              </div>
            </div>
          </div>
          <div class="img" v-if="item.attachment == 2"> 
            <div v-if="item.image_list.length == 4">
              <div>
                <img class="img_content" :src="item.image_list[0]" @click="ImagePreview(item.image_list,0)"  >
                <img class="img_content" :src="item.image_list[1]" @click="ImagePreview(item.image_list,1)"  >
              </div>
              <div>
                <img class="img_content" :src="item.image_list[2]" @click="ImagePreview(item.image_list,2)"  >
                <img class="img_content" :src="item.image_list[3]" @click="ImagePreview(item.image_list,3)"  >
              </div>
            </div>
            <div v-else >
              <img v-for="(item_1, index) in item.image_list" :key="index" class="img_content" :class="{'img_content_1':item.image_list.length == 1}" :src="item_1" alt="" @click="ImagePreview(item.image_list,index)">
            </div>
            
          </div>
          <div class="video" v-bind:class="{'video_1':item.video_heng_shu == 1,'video_2':item.video_heng_shu == 0}" v-if="item.attachment == 1">
            <video :src="item.video" :poster="item.image_list[0]" x5-video-player-type="h5" x5-video-orientation="landscape|portrait" controls="controls"></video>
          </div>
          <div class="usage">
            <span class="time">{{item.timestamp | timeformat()}}</span>
          <div class ='news'>
              <div class="usage_news" @click="handleToDetails(item.tid)" >
                <img class="usage_news_img" src="../../assets/images/news.png" alt="">
                <span>{{item.replies}}</span>
              </div>
              <div class="usage_news" @click="addZan(item,1)">
                <img class="usage_news_img" v-if="item.is_zan == 1" src="../../assets/images/icon_love_selection.png" alt="">
                <img class="usage_news_img" v-else src="../../assets/images/icon_love_normal.png" alt="">
                <span>{{item.zan}}</span>
              </div>
              <div class="usage_news usage_news_right" @click="addZan(item,2)">
                <img class="usage_news_img" v-if="item.is_zan == 2"  src="../../assets/images/icon_stepon_selected.png" alt="">
                <img class="usage_news_img" v-else src="../../assets/images/icon_stepon_normal.png" alt="">
                <span>{{item.cai}}</span>
              </div>
          </div>
          </div>
          <div class="information" v-if="item.post ? item.post.length:true" @click="handleToDetails(item.tid)">
            <div class="information_text" v-for="(item_1,key_1) in item.post" :key="key_1" >
              <span class="information_title">{{item_1.author}}：</span>
              <span class="information_content">{{item_1.message}}</span>
            </div>
          </div>
        </div>
      </div>
    </van-list>
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" cancel-text="取消" @cancel="onCancel" />
  </div>
</template>
<script>

import { ImagePreview,Toast } from 'vant';
import {getFromNom} from '@js/moment'
export default {
  props:['itemdata'],
  data() {
    return {
      items:{},
      pic:'',
      views:'',
      replies:'',
      name:this.$route.query.name,
      down:require('../../assets/images/arrow_down.png'),
      up:require('../../assets/images/arrow_up.png'),
      icon_iscarvip:require('../../assets/images/icon_iscarvip.png'),
      icon_label_d:require('../../assets/images/icon_label.png'),
      show: false,
      loading:false,
      selectActionItem:null,    // 更多操作 - 对象
      finished:false,
      actions: [
        {
          name: '私聊'
        },
        {
          name: '关注'
        }
      ],      
      showTotal: true,
      exchangeButton: false,
      showExchangeButton: false,
      introduce: "",
      datalist:[], // 列表数据
      allpage:-1,
      parameter:{
        token:this.$store.state.userData,
        type:0,
        typeid:this.$route.query.typeid,
        order:0,
        page_size:10,
        page_index:0     
      }
    };
  },
  watch: {
    introduce: function() {
      this.$nextTick(
        function() {
          let rem = parseFloat(this.getRem());
          if (!this.$refs.desc) {
            return;
          }
          let descHeight = window
            .getComputedStyle(this.$refs.desc)
            .height.replace("px", "");
          if (descHeight > 5.25 * rem) {
            // console.log('超过了四行');
            // 显示展开收起按钮
            this.showExchangeButton = true;
            this.exchangeButton = true;
            // 不是显示所有
            this.showTotal = false;
          } else {
            // 不显示展开收起按钮
            this.showExchangeButton = false;
            // 没有超过四行就显示所有
            this.showTotal = true;
            // console.log('showExchangeButton', this.showExchangeButton);
            // console.log('showTotal', this.showTotal);
          }
        }.bind(this)
      );
    }
  },
  mounted() {
    document.title = 'E区专题';
    this.squaresItem();
  },
  methods: {

    handleToDetails(tid){
      let url = `${this.$store.state.indexUrl+'square/details?tid='+tid+'&'+this.$store.state.appParams}`;
    
      if(!this.$store.state.userData){
      //    window.location.href= "teldapp://login"
        let frame = document.createElement("frame");
            frame.src ="teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);      
      }else {
          let json = {url:url,share:'0',keyboard:'1'};
          window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)
        //  this.$router.push({path:'/square/details',query:{tid:tid}})
      }
     // this.$router.push({path:'/square/details',query:{tid:tid,name:this.name}})
    },
    onClickLeft(){
      this.$router.go(-1)
    },
    onLoad(){
      if(this.datalist.length){
        this.parameter.page_index++;
        this.squaresItem();
      }
      if(this.parameter.page_index == this.allpage){
        this.finished = true
      }
      
    },
    // 跳转我的页面
    handleToMyPage(item){
      if(!this.$store.state.userData){
        //  window.location.href= "teldapp://login"
          let frame = document.createElement("frame");
              frame.src ="teldapp://login";
          document.documentElement.childNodes[0].appendChild(frame);        
          return null;
      }
      if(item.is_self == 1){
        this.$router.push({path:'/my/index/square'})
      }else {
        this.$router.push({path:'/my/index/square',query:{uid:item.authorid}})
      }
      //this.$router.push({path:'/my/index/square',query:{uid:''}})
    },
    // 图片浏览
    ImagePreview(v,key){
      ImagePreview({
        images:v,
        startPosition:key,
        loop:false
      })
    },
    // 点赞或踩
    addZan(item,type){
      let tid = item.tid,
          token = this.$store.state.userData;
      Toast.loading({
          message: '加载中...',
          forbidClick: true
      });             
      this.api.addZan({token,tid,type}).then(res => {
        Toast.clear();
        if(res.err_code == 0){
          if(item.is_zan == 1){
            if(type == 1){
              item.zan = parseInt(item.zan)-1
              item.is_zan = 0;
            }else if(type == 2){
              item.is_zan = type;
              item.zan = parseInt(item.zan)-1
              item.cai = parseInt(item.cai)+1
            }
          }else if(item.is_zan == 2){
              if(type == 1){
                item.is_zan = type;
                item.zan = parseInt(item.zan)+1
                item.cai = parseInt(item.cai)-1
              }else if(type == 2){
                item.is_zan = 0;
                item.cai = parseInt(item.cai)-1
              }     
          }else {
            if(type == 1){
              item.zan = parseInt(item.zan)+1

            }else if(type == 2){
              item.cai = parseInt(item.cai)+1
            }
            item.is_zan = type;
          }
          if(item.is_zan){

          }
          
          //Toast(res.data.msg)
        }else {
          Toast(res.err_msg)
        }
      })
    },
    // 广场列表
    squaresItem(){
        this.api.squaresItem(this.parameter).then( res => {
          if(res.err_code == 0){
            this.allpage = res.data.total_page;
            this.pic = res.data.banner;
            this.views = res.data.views;
            this.replies = res.data.replies;
            this.items=res.data;
            this.datalist =  [...this.datalist,...res.data.thread];
          }
          
          this.loading = false;
        })
    },
    // 内容展开
    showTotalIntro() {
      this.showTotal = !this.showTotal;
      this.exchangeButton = !this.exchangeButton;
    },
    //文章高度
    getRem(){
      const defaultRem = 16;
      let winWidth = window.innerWidth;
      let rem = (winWidth / 375) * defaultRem;
      return rem;
    },
    //文章查看更多
    handleClickAll() {
      this.dispalyAll = true;
    },
    //文章查看收起
    handleClick() {
      this.dispalyAll = false;
    },
    // 关注操作
    addFollow(followuid){
      this.api.addFollow({
        followuid,
        token:this.$store.state.userData,
      }).then( res => {
        if(res.err_code == 0){
          Toast(res.data.msg);
        }
      })
    },    
    onSelect(item){

      switch(item.name){
        case '关注':
          this.addFollow(this.selectActionItem.authorid);
          break;
        case '取消关注':
          this.addFollow(this.selectActionItem.authorid);
          break;  
        case '私聊':
            let url = `${this.$store.state.indexUrl+'other/wechat?uid='+this.selectActionItem.authorid+'&name='+this.selectActionItem.author+'&'+this.$store.state.appParams}`,
                json = {url:url,share:'0',keyboard:'1'};
 
                window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)

            //this.$router.push({path:'/other/wechat',query:{uid:this.selectActionItem.authorid}})
          break;
      }
  
    
      this.show = false;
      
    },
    onCancel(){
      this.show = false;
    },
    // 是否关注
    isFollow(uid){
      this.api.isFollow({
        uid:uid,
        token:this.$store.state.userData
      }).then( res => {
        if(res.err_code == 0){
          
          
          switch(res.data.status){
            case 0:
                this.actions[1].name = '关注'
              break;
            case 1:
                this.actions[1].name = '取消关注'
              break;
            case 2:
                this.actions[1].name = '取消关注'
              break    
          }
          this.$nextTick(()=>{
            this.show = true;
          })
        }else {

        }
      })
    },    
    // 更多  
    handleMore(item){
      if(!this.$store.state.userData){
          console.log('window.location.href= "teldapp://login"');  
         // window.location.href= "teldapp://login"
            let frame = document.createElement("frame");
                frame.src ="teldapp://login";
            document.documentElement.childNodes[0].appendChild(frame);         
          return null;
      }
      this.selectActionItem = item;
      this.isFollow(item.authorid)
     
      //this.show = true;
    }
  },
  filters:{
    timeformat(v){
      if(v){
        return getFromNom(v*1000)
      }else {
        return ''
      }
    }
  }
};
</script>
<style lang="less" scoped rel="stylesheet/less">
.square {
  display: flex;
  flex-direction: column;
  padding-bottom:40px; 
  .bg{
    // height: 286px;
    width: 100%;
    position: relative;
    .bg-image{
      width: 100%;
      height: 100%;
      // object-fit: cover;
    }
    .other{
      justify-content: space-between;
      position: absolute;
      bottom: 12px;
      left: 15px;
      .views{
          background-image: url('../../assets/images/see.png');
          display: inline-block;
          color: #424E77;
          background-size: 16px 16px;
          background-repeat: no-repeat;
          padding-left: 18px;
          background-position-y: 50%;
          
          font-size: 15px;
          margin-right: 20px;
      }
      .replies{
          background-image: url('../../assets/images/news.png');
          display: inline-block;
          color: #424E77;
          background-size: 14px 14px;
          background-repeat: no-repeat;
          padding-left: 20px;
          background-position-y: 50%;   
          font-size: 12px;                     
      }                            
  }
  }

  .list{
    padding: 0 15px;
    margin-bottom: 42px;
  }
}
.ad-box{
  position: relative;
  img{
    width: 100%;
    border-radius: 8px;
  }
  span{
    position: absolute;
    top: 0px;
    right: 0px;
    display: inline-block;
    color: #ffffff;
    background-color: #0bc7f0;
    padding: 0 3px;
    border-radius: 0 8px 0 8px;
    font-size: 13px;


  }
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* flex-wrap: nowrap; */
  width: 100%;
  align-items: center;
}
.header_center {
  display: flex;
  flex-direction: column;
  width: 250px;
}
.avatar-box{
  position: relative;
  margin-right: 13px;
}
.portrait_img {
  width: 42px;
  height: 42px;
  border-radius: 100% 100%;
  flex: none;
  border: 1px solid;
}
.isvip{
  position: absolute;
  width: 14px;
  height: 14px;
  bottom: 6px;
  right: 1px;
}
.header_center_label {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-top: 4.5px;
}
.header_center_name {
  font-size: 16px;
  color: #4e5578;
  margin-top: 16px;
}
.label_grade {
  // width: 92.5px;
  height: 15px;
  padding-right: 4px;
  border: 1px solid #0ec6f1;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
}
.label_grade_img {
  width: 10px;
  border: none;
  height: 9px;
}
.label_grade_name {
  font-size: 10px;
  color: #0ec6f1;
  padding: 0 5px;
}
.isCarvip{
  background-image: url('../../assets/images/icon_iscarvip.png');
  background-repeat: no-repeat;
  background-size: 11px 10px;
  background-position-y: 46%;
  background-position-x: 1px; 
  padding-left: 14px;
}
.label_heat {
  // width: 37.5px;
  padding: 0 5px;
  height: 15px;
  border: 1px solid #0ec6f1;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}
.label_heat_img {
  width: 8px;
  height: 13px;
}
.content {
  margin: 5px 0 0 55px;
  display: flex;
  flex-direction: column;
  align-items: left;
}
.content_display_all {
  max-width: 295px;
  word-break: break-all;
  margin-bottom: 7.5px;
  overflow: visible;
}
.total-introduce {
  height: auto;
  overflow: hidden;
  font-size: 15px;
  color: #434343;
  margin: 5px;
  .intro-content {
    .merchant-desc {
      width: 100%;
      line-height: 11px;
    }
    .name{
        color: #0EC6F1;
    }
  }
  .unfold {
    display: block;
    z-index: 11;
    float: right;
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: center;
    img {
         width: 20px;
         height: 20px;
    }
  }
}
.detailed-introduce {
  font-size: 15px;
  color: #434343;
  position: relative;
  overflow: hidden;
  .intro-content {
    // 最大高度设为4倍的行间距
    max-height: 125px;
    font-size: 15px;
    line-height: 20px;
    word-wrap: break-word;
    /*强制打散字符*/
    word-break: break-all;
    background: #ffffff;
    /*同背景色*/
    color: #ffffff;
    overflow: hidden;
    .merchant-desc {
      width: 100%;
      line-height: 10px;
    }
    &:after,
      // 这是展开前实际显示的内容
      &:before {
      content: attr(title);
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      color: #434343;
      // overflow: hidden;
    }
    // 把最后最后一行自身的上面三行遮住
    &:before {
      display: block;
      overflow: hidden;
      z-index: 1;
      max-height: 95px;
      background: #ffffff;
    }
    &:after {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      height: 40px;
      /*截取行数*/
      -webkit-line-clamp: 4;
      text-overflow: ellipsis;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      /*行首缩进字符数，值为[(截取行数-1)*尾部留空字符数]*/
      text-indent: -12em;
      /*尾部留空字符数*/
      padding-right: 4em;
    }
    .unfold {
      z-index: 11;
      width: 100%;
      height: 20px;
      outline: 0;
      position: absolute;
      right: 0;
      bottom: 0;
      display: flex;
      justify-content: center;
      img {
         width: 20px;
         height: 20px;
      }
    }
  }
}
.img{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: 55px
}
.video{
  margin-left: 55px;
  width: 82.5px;
  height: 82.5px;
  video{
    width: 100%;
    height: 100%;
  }
}
  .video_1{
    width: 183px;
    height: 240px;
  }
  .video_2{
    width: 230px;
    height: 160px;
  }  
.img_content{
  width: 82.5px;
  height: 82.5px;
  margin-bottom: 5px;
  object-fit:cover
}
.img_content_1{
  width: 170px;
  height: 170px;
  object-fit: cover;
}
.img_content:nth-of-type(2) {
  margin-left: 5px
}
.img_content:nth-of-type(5) {
  margin-left: 5px
}
.img_content:nth-of-type(6) {
  margin-left: 5px
}
.img_content:nth-of-type(8) {
  margin-left: 5px
}
.img_content:nth-of-type(9) {
  margin-left: 5px
}
.img_content:nth-of-type(3) {
  margin-left: 5px
}
.usage{
  margin-left: 55px;
  height: 20px;
  display: flex;
  align-items: center;
  font-size: 14px;
  justify-content: space-between;
  margin-top: 12.5px;
}
.news{
  display: flex;
  flex-direction: row;
}
.time{
  color: #888888;
  font-size: 12px;
}
.usage_news{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.usage_news_img{
  width: 16px;
  height: 16px;
  margin-right: 3px
}
.usage_news>span {
  color: #424E77;
  margin-right: 22px;
  font-size: 12px;
}
.usage_news_right{
  margin-right: 12.5px;
}
.information{
  background-color: #f0f0f0;
  border-radius: 5px;
  margin:18px 0 0 55px;
  padding-bottom: 12.5px;
}
.information_text {
  padding: 12.5px 15px 0;
  font-size: 13px
}
.information_title{
  color: #424E77;
}
.information_content{
  color: #111111 ;
  word-break: break-all
}
</style>